import React, { useState, useEffect } from 'react';
import './App.css';
import { Button, InputLabel, Input, FormControl } from '@material-ui/core';
import Todo from './Todo';
import db from './firebase';
import firebase from 'firebase';

function App() {
  const [todos, setTodos] =useState([]); 
  const [input, setInput] = useState('');
  console.log(input);

  //when the app loads, we need to listen to the database and fetch new tools as they get added/ removed
  useEffect(() => {
    //This code here.... fires when the app.js loads
    db.collection('todos').orderBy('timestamp','desc').onSnapshot(snapshot => {
      //console.log(snapshot.docs.map(doc => doc.data()));
      setTodos(snapshot.docs.map(doc => doc.data().text))
    })
  }, []);

  const addTodo = (event) => {
    //this will fire off when we click the button
    event.preventDefault(); //It will stop refreshing the page on button click 
    //console.log('I am woring-- addTodo function!!');
    db.collection('todos').add({
      text: input,
      timestamp: firebase.firestore.FieldValue.serverTimestamp()
    })

    setTodos([...todos, input]);
    setInput(''); // clear up the input aftre clicking todo button
  }

  return (
    <div className="App">
      <h1>Hello Prachi!! Welcome to TOdo app.. </h1>
      <form> 
        <FormControl>
          <InputLabel>Write a TODO:</InputLabel>
          <Input value={input} onChange={event => setInput(event.target.value)}/>        
        </FormControl>
        <Button disabled={!input} type="submit" onClick={addTodo} variant="contained" color="primary">
          Add Todo
        </Button> 
          {/*<button type="submit" onClick={addTodo}>Add Todo</button>*/}
      </form>
      
      <ul>
        {todos.map(todo => (
          <Todo text={todo}/>
          //<li>{todo}</li>
        ))}
      </ul>
    </div>
  );
}

export default App;
